import { ShareOutcomeService } from './../../../+builder/services/shareOutcome.service';
import { Component, Input, Output, OnInit, EventEmitter, ViewEncapsulation } from '@angular/core';
import { JSONBuilder } from '../../../+builder/services/JSONBuilder.service';
import { AnalyticService } from '../../services/analytic.service';
@Component({
	selector: 'click-button',
	template: `
			 <div class="text-center" *ngIf="data.visible">
				<div>
					<button class="btn prime-action  next-step sliding-next"
						(click)="controlOutput.emit($event)"
						*ngIf="page?.type !== 'Result'"
					>
						{{data.props.title}}
					</button>
						<a class="btn prime-action  next-step sliding-next "
 						(click)="controlOutputOnLive($event)"
 							*ngIf="_JSONBuilder.getJSONBuilt().templateType =='Recommendation' && page?.type === 'Result'"
 							[attr.href]="_JSONBuilder.getJSONBuilt().navigate_Url" target="_blank"
 					>
 						{{_outcomeService.getSelectedFormula().units.preValue}}
 					</a>
 					
 					<a class="btn prime-action  next-step sliding-next "
 						(click)="controlOutputOnLive($event)"
 							*ngIf="_JSONBuilder.getJSONBuilt().templateType =='Numerical' && page?.type === 'Result'"
 							[attr.href]="_JSONBuilder.conditional.conditionalVar?_JSONBuilder.conditional.url:_JSONBuilder.getJSONBuilt().navigate_Url" target="_blank"
 					>
 						{{_JSONBuilder.conditional.conditionalVar?_JSONBuilder.conditional.buttonText:data.props.title}}
 					</a>
 					
				</div>
			</div>
			 `,
	encapsulation: ViewEncapsulation.None
})

export class Button implements OnInit {
	@Input() data: any;
	@Input() page: any;
	@Output() controlOutput = new EventEmitter();
	value: string;
	 cta_engagement_click: boolean = false;
	constructor(private _analyticService: AnalyticService,
		private _JSONBuilder: JSONBuilder,
		private _outcomeService: ShareOutcomeService) {

	}
	ngOnInit() {
		//
	}
	controlOutputOnLive(event: any) {
		if (this._JSONBuilder.getJSONBuilt().status !== 'LIVE') { event.preventDefault(); }
		this.controlOutput.emit(event);
		if (!this.cta_engagement_click) { this.ctaEngagment(); }
	}
	ctaEngagment() {
      this._analyticService.updateCalcVisitor('URL Redirect').subscribe(
        (response) => {
          this.cta_engagement_click = true;
        },
        (error) => { console.log('error', error); }
      );
    }
}




