import { Component, Input, Output, EventEmitter, OnInit, ViewEncapsulation,AfterViewInit } from '@angular/core';
import { AnalyticService } from '../../services/analytic.service';
import { Subscription } from 'rxjs/Subscription';
import { JSONBuilder } from '../../../+builder/services/JSONBuilder.service';
import { FormGroup } from '@angular/forms';

declare var jQuery: any;
@Component({
  selector: 'textfield',
  encapsulation: ViewEncapsulation.None,
  templateUrl: './textfield.component.html'
})
export class TextField implements OnInit,AfterViewInit {
  @Input() data: any;
  @Input() devMode: any;
  @Input() form: FormGroup;
  @Output() controlOutput = new EventEmitter();
  value: string;
  inputField: any;
  showButton: boolean = false;
  ValidationMessage: string;
  private savingData: Subscription;

  constructor(private _analyticService: AnalyticService, private jsonBuilderHelper: JSONBuilder) {

  }
  ngAfterViewInit(){
    //auto input grow 
    jQuery.fn.textWidth = function(text, font) {
    
        if (!jQuery.fn.textWidth.fakeEl) jQuery.fn.textWidth.fakeEl = jQuery('<span>').hide().appendTo(document.body);
        
        jQuery.fn.textWidth.fakeEl.text(text || this.val() || this.text() || this.attr('placeholder')).css('font', font || this.css('font'));
        
        return jQuery.fn.textWidth.fakeEl.width();
    };

    jQuery('.submit-textbox input').on('input', function() {
        var inputWidth = jQuery(this).textWidth();
        jQuery(this).css({
            width: inputWidth
        })
    }).trigger('input');


    function inputWidth(elem) {
        elem = jQuery(this);
        console.log(elem)
    }

    var targetElem = jQuery('.submit-textbox input');

    inputWidth(targetElem);
  }
  ngOnInit() {
    this.data.props.currentValue = this.data.props.defaultValue;
    this.data.props.currentlabel = this.data.props.defaultValue;
    this.inputField = this.data.props.defaultValue;
  }
  submitTextfield(e: any) {
      e.preventDefault();
      if (!this.devMode && !this.form.controls[this.data._id].errors) {
        this.controlOutput.emit(true);
      }
  }
  get isValid() {
    if (this.form.controls[this.data._id].errors) {

      if (this.form.controls[this.data._id].errors['required']) {
        this.ValidationMessage = this.data.config.validations.required.message + '!';
        return false;
      } else if (this.form.controls[this.data._id].errors['minval']) {
        this.ValidationMessage = 'Minimum Required Value is ' + this.data.props.minVal;
        return false;
      } else if (this.form.controls[this.data._id].errors['maxval']) {
        this.ValidationMessage = "Can't Go beyond " + this.data.props.maxVal + '!';
        return false;
      } else if (this.form.controls[this.data._id].errors['minlength']) {
        this.ValidationMessage = 'Minimum ' + this.form.controls[this.data._id].errors['minlength'].requiredLength + ' character required!';
        return false;
      } else if (this.form.controls[this.data._id].errors['EmailError']) {
        this.ValidationMessage = 'Not A Valid Email!';
        return false;
      } else {
        return true;
      }

    } else {
      return true;
    }

  }

  keyUp() {
    // if (this.data.props.currentValue.length > 0) {
      this.showButton = true;
    // }
    if (this.data.config.type == 'number') {
      if(parseFloat(this.data.props.minVal)<=this.inputField && this.inputField<=parseFloat(this.data.props.maxVal))
        this.data.props.currentValue = this.inputField;
      // if (!(this.form.controls[this.data._id].touched && !this.isValid))
    } else
      this.data.props.currentValue = this.inputField;
  }

  keyPressed(e: any) {
    var code = e.keyCode || e.which;
    if (code == 13) {
      if (this.jsonBuilderHelper.getJSONBuilt().template === 'one-page-card') {
        this.controlOutput.emit(true);
        e.preventDefault();
        return false;
      }
    }
  }

  onBlur() {
    if (this.data.props.currentValue.length > 0) {
      this.data.props.scale = true;
    }
    if (this.jsonBuilderHelper.getJSONBuilt().status == 'LIVE') {
      /* if key is undefined then push in array */
      if (this._analyticService.getVisitorKey() == '')
        this._analyticService.setVisitorAnswers(this.data);
      /*  */
      if (this._analyticService.getVisitorKey()) {
        this._analyticService.saveStats(this.jsonBuilderHelper.getJSONBuilt()._id, this.data)
          .subscribe(
          (response: any) => {
            this._analyticService.setVisitorKey(response.key);
            if (response.status == 'Array Updated')
              this._analyticService.reInitVisitorAnswers();
          },
          (error: any) => {
            console.log(error);
          }
          );
      }

    }
  }
}



