import { Checkbox } from './../controls/checkbox/checkbox.component';
import { Result } from './../components/result.directive';
import { Injectable } from '@angular/core';
import { JSONBuilder } from '../../+builder/services/JSONBuilder.service';
import { FormulaService } from '../../+builder/services/formula.service';
declare var math: any;
@Injectable()
export class TemplateRendererService {
  staticControls: any = {};
  constructor(private _jsonBuilderHelper: JSONBuilder,
    private formulaService: FormulaService) {
    //code
    this.setStaticControls();
  }

  setStaticControls() {
    let self = this;
    self._jsonBuilderHelper.getJSONBuilt().pages.forEach(function (page) {
      if (page.type != 'Questionnaire') {
        self.staticControls[page.type] = {};
        page.sections.forEach(function (section) {
          section.items.forEach(function (item) {
            // check for result outputs
            if (section.title === 'Result') {
              self.staticControls[page.type][section.title] = section;
            }
            else {
              if (self.staticControls[page.type].hasOwnProperty(item.type)) {
                let itemObj: any;
                let i: number = 1;
                while (itemObj == undefined) {
                  if ((self.staticControls[page.type][item.type + '_' + i]))
                    i++;
                  else {
                    self.staticControls[page.type][item.type + '_' + i] = item; itemObj = item;
                  }
                }
              } else {
                self.staticControls[page.type][item.type] = item;
              }
            }
          });
        });
      }
    });
  }

  getStaticControls(): any {
    //console.log('staticControls', this.staticControls);
    return this.staticControls;
  }

  getBackground(what: string): string {
    var landingPage = this._jsonBuilderHelper.getJSONBuilt().pages.filter((page: any) => page.type == 'Landing');
    if (landingPage) {
      if (what == 'img')
        return landingPage[0].bgImage && landingPage[0].bgImageVisible ? ('url(' + landingPage[0].bgImage + ')') : '';
      else
        return landingPage[0].bgColor ? landingPage[0].bgColor : '';

    } else
      return '';
  }

  /** update MIN and MAX of result-output */
  computeMinMax() {
    if (this._jsonBuilderHelper.getJSONBuilt().templateType != 'Recommendation') {
      for (let formula in this._jsonBuilderHelper.getJSONBuilt().formula) {
        this.staticControls.Result.Result.items[formula].props.minVal = this.formulaVal(this._jsonBuilderHelper.getJSONBuilt().formula[formula], 'min');
        this.staticControls.Result.Result.items[formula].props.maxVal = this.formulaVal(this._jsonBuilderHelper.getJSONBuilt().formula[formula], 'max');
        this.staticControls.Result.Result.items[formula].options[0].attr.class = this.staticControls.Result.Result.items[formula].props.minVal;
        this.staticControls.Result.Result.items[formula].options[this.staticControls.Result.Result.items[formula].options.length - 1].attr.style
          = this.staticControls.Result.Result.items[formula].props.maxVal;
      }
    }
  }

  formulaVal(formula: any, type: string) {

    /* for minvalues */
    let rawFormula = this.formulaService.replaceRs(formula.result);
    let questionStringMin = rawFormula.replace(/(Q[\d]+)/g, (match) => {
      let possibleMinVal: number = Number.MAX_VALUE;
      let possibleMaxVal: number = Number.MIN_VALUE;
      /* index of question */
      let index = Number(match.substring(1));
      let currentQuesObject = this._jsonBuilderHelper.getTemplateQuestionare()[index - 1];
      /* evaluate min value * */
      if (currentQuesObject) {
        if (currentQuesObject && (currentQuesObject.type == 'slider' || (currentQuesObject.type == 'textfield' && currentQuesObject.config.type == 'number'))) {
          /*min max conditions */
          if (type == 'min') possibleMinVal = parseFloat(currentQuesObject.props.minVal);
          else possibleMaxVal = parseFloat(currentQuesObject.props.maxVal);
        } else if (currentQuesObject.type == 'checkbox') {
          possibleMaxVal = 0;
          for (var t = 0; t < currentQuesObject.options.length; t++) {
            /*min max conditions */
            if (type == 'min') {
              if (parseFloat(currentQuesObject.options[t].value) <= possibleMinVal) { possibleMinVal = currentQuesObject.options[t].value; };
            } else {
              possibleMaxVal += parseFloat(currentQuesObject.options[t].value);
            }
          }
        } else {
          for (var t = 0; t < currentQuesObject.options.length; t++) {
            /*min max conditions */
            if (type == 'min') {
              if (parseFloat(currentQuesObject.options[t].value) <= possibleMinVal) { possibleMinVal = currentQuesObject.options[t].value; };
            } else {
              if (parseFloat(currentQuesObject.options[t].value) >= possibleMaxVal) { possibleMaxVal = currentQuesObject.options[t].value; };
            }
          }
        }
      }
      return ((type == 'min') ? possibleMinVal : possibleMaxVal);
    });
    try {
      let val = math.eval(questionStringMin);
      val = val.toFixed(formula.decimal);
      return val;
    } catch (e) {
      return undefined;
    }
  }

}



