import { Component, Input, Output, OnInit, EventEmitter, ViewEncapsulation } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { CustomValidator } from '../../services/customValidation';
import { AnalyticService } from '../../services/analytic.service';
import { JSONBuilder } from '../../../+builder/services/JSONBuilder.service';
import { Subscription } from 'rxjs/Subscription';
declare var ga: Function;
declare var jQuery: any;

@Component({
  selector: 'leadform_question',
  templateUrl: './leadform_question.component.html',
  encapsulation: ViewEncapsulation.None
})
export class LeadFormQuestion implements OnInit {
  @Input() data: any;
  @Input() devMode: any;
  @Output() controlOutput = new EventEmitter();
  obj: any = {};
  form: FormGroup;
  showbutton: boolean = false;
  private savingData: Subscription;
    iconObj = {
      'name': 'person',
      'firstName': 'person',
      'tel': 'phone',
      'others': 'public',
      'lastName': 'public',
      'email': 'mail_outline'
  };
  constructor(private _analyticService: AnalyticService, private jsonBuilderHelper: JSONBuilder) {

  }

  ngOnInit() { }

  onTouched(i: any) {
    jQuery('#touched' + this.data._id + i).show();
  }

  onSubmit(form: FormGroup) {
    let objArray: any[] = [];
    for (var i in this.form.controls) {
      jQuery('#touched' + this.data._id + i).show();
      this.form.controls[i].markAsTouched();
    }
    if (form.valid) {
      this.controlOutput.emit(true);
    }

    for (let field of this.data.fields) {
      this.obj[field.name] = field.value;
      objArray.push({ field_name: field.name, value: field.value });
    }

    if (this.jsonBuilderHelper.getJSONBuilt().status == 'LIVE' && form['_status'] == 'VALID') {
      //SEND Analytic
      //Ours
      ga('devteam.send', 'event', this.jsonBuilderHelper.getJSONBuilt().name + ' ' + this.jsonBuilderHelper.getOtherVisibleLeadForm() + ' Leadform', 'Submitted', this.jsonBuilderHelper.getJSONBuilt().url);
      //Users
      if (this.jsonBuilderHelper.getJSONBuilt().ga)
        ga('userCustom.send', 'event', this.jsonBuilderHelper.getJSONBuilt().name + ' ' + this.jsonBuilderHelper.getOtherVisibleLeadForm() + ' Leadform', 'Submitted', this.jsonBuilderHelper.getJSONBuilt().url);

      this._analyticService.saveLead(this.jsonBuilderHelper.getJSONBuilt()._id, this.obj, objArray)
        .subscribe(
        (response: any) => {
          this._analyticService.setVisitorKey(response.key);
          ga('devteam.send', 'pageview', '/' + response.lead);
        },
        (error: any) => {
          console.log(error);
        }
        );
    }

  }
  skip(event: any) {
    if (this.devMode) { event.preventDefault(); } else { this.controlOutput.emit(true); }
  }
  showButton() {
    this.showbutton = true;
  }
  formGroup(): FormGroup {
    if (!this.form || this.data.fields.length > Object.keys(this.form.controls).length) {
      let group: any = {};
      this.data.fields.forEach((field: any, index: number) => {
        let validators: any[] = [];

        if (field.validations.required.status === true) {
          validators.push(Validators.required);
          if (field.type === 'email')
            validators.push(CustomValidator.emailFormat);
          if (field.type === 'tel')
            validators.push(CustomValidator.phoneNumer);
        }
        // if(field.validations.maxLength.status===true)
        //   validators.push(Validators.maxLength);

        group[index] = new FormControl(field.value || '', Validators.compose(validators));
      });
      this.form = new FormGroup(group);
      return this.form;
    }
    return this.form;
  }

  //   get isRequired() {
  //   if(this.formGroup().errors) {
  //         return !this.formGroup().controls['required'];
  //     }else {
  //         return true;
  //     }
  // }
  //   get isValid() {
  //     return this.formGroup().valid;
  //   }

}



