import {Component, Input, Output, AfterViewInit, ViewEncapsulation, EventEmitter} from '@angular/core';
import { AnalyticService } from '../../../templates/services/analytic.service';
import { JSONBuilder } from '../../../+builder/services/JSONBuilder.service';
declare var jQuery: any;
@Component({
  selector: 'result_redo',
  encapsulation: ViewEncapsulation.None,
  template: `
    <div class="redo-link">
		<ul>
			<li><a  id="refresh-button" (click)="redoResult()"><i class="material-icons">replay</i></a></li>	
        </ul>
	</div>
			
`
})
export class RedoComponent implements AfterViewInit {
  @Input() data: any;
  @Input() devMode: any;
  @Output() controlOutput = new EventEmitter();
  value: string;
  constructor(private jsonBuilderHelper: JSONBuilder,
             private _analyticService: AnalyticService
   ) {
    // yoyo
  }
   redoResult() {
    if (!this.devMode) {
        this.jsonBuilderHelper.resetQuestions();
        this.redo_count();
        this.controlOutput.emit(true);
     }
   }
   redo_count() {
    this._analyticService.redoCountCalcVisitor().subscribe(
      (response) => {
        // yoyo
       },
      (error) => { console.log('error', error); }
    );
  }
  ngAfterViewInit() {}
}



